.button-wrapper {
    height: 4em;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: lighter;
    font-size: 1.4em;
    background-color: #e0e1e6;
    color: #888;
    flex: 1;
    outline: 1px solid #888;
    cursor: pointer;
    transition: all ease .2s;
}

.button-wrapper:hover {
    background-color: #cccccc;
    color: #111;
}


.operator {
    background-color: #fe9241;
    color: white;
}

.operator:hover {
    background-color: #f87d1f;
}